<template>
    <div>
      <table border="1" >
        <thead>
        <tr>
          <th>标题</th>
          <th>时间</th>
          <th>作者</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="msgs in hireMessage">
          <td>{{msgs.title}}</td>
          <td>{{msgs.time}}</td>
          <td>{{msgs.author}}</td>
          <td><a href="javascript:;" @click="deleteInfo(msgs.id)">删除</a></td>
          <td><a href="javascript:;" @click="updateInfo(msgs.id)">修改</a></td>
        </tr>
        </tbody>
      </table>

      更新标题<input v-model="hireMsg.title" type="text"><br>
      更新日期<input v-model="hireMsg.time" type="text"><br>
      更新作者<input v-model="hireMsg.author" type="text"><br>
       <a href="javascript:;" @click="submitupdateInfo">提交更新</a><br>
      <a href="#/info/addHire">添加招聘</a>
      <router-view></router-view>
      <cute></cute>
    </div>
</template>

<script>
  import Cute from './Cute';
  import Footer from './Footer';
    export default {
        name: "Informations",
        data(){
            return{
                hireMessage:[],
                hireMsg:'',
            }
        },
        methods:{
            selectAll(){
                this.$http.get(" http://localhost:8080/lianxi/a/selectAllHire2").then((res)=>{
                    console.log(res.data);
                    this.hireMessage=res.data;
                });
            },

            deleteInfo(id){

                this.$http.get(" http://localhost:8080/lianxi/a/deleteInfo?id="+id).then((res)=>{
                  alert(res.data);

                });
            },

            updateInfo(id){

                this.$http.get(" http://localhost:8080/lianxi/a/selectById?id="+id).then((res)=>{
                    this.hireMsg=res.data;
                });
            },

            submitupdateInfo(){

                this.$http.post(" http://localhost:8080/lianxi/a/updateInfo",this.hireMsg).then((res)=>{
                    alert(res.data);
                });
            }





        },
        components:{
            Cute,
            Footer,
        },

        created() {
            this.$http.get(" http://localhost:8080/lianxi/a/selectAllHire2").then((res)=>{
                console.log(res.data);
                this.hireMessage=res.data;
            });
        },
        // 监听,当路由发生变化的时候执行
        watch: {
            $route: {
                handler: function(val, oldVal){
                    console.log("路径变化"+val.path);
                    if(val.path=='/info'){
                       this.selectAll();
                    }
                },
                // 深度观察监听
                deep: true
            }
        }



    }
</script>

<style scoped>

</style>



