import Vue from 'vue'
import Router from 'vue-router'
import Ljm from '../components/Ljm'
import Informations from '../components/Informations'
import AddHireMessage from '../components/AddHireMessage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/ljm',
      name: 'ljm',
      component: Ljm
    },
    {
      path: '/info',
      name:'Informations',
      component: Informations,
      children:[{
        path:'addHire',
        component:AddHireMessage
      },
        {
          path:'updateHire',
          component:AddHireMessage
        }
      ]
    }


  ]
})



