<template>
    <div>
      <h1>添加招聘信息</h1>
      <form action="">
        标题<input v-model="hire.title" type="text" placeholder="请输入标题"><br>
        副标题<input v-model="hire.viceTitle" type="text" placeholder="请输入副标题"><br>
        作者<input v-model="hire.author" type="text" placeholder="请输入作者"><br>
        招聘信息<input v-model="hire.content" type="text" placeholder="请输入招聘信息"><br>
         <input type="button" @click="addHireInfo" value="添加招聘">
      </form>
    </div>
</template>

<script>
    export default {
        name: "AddHireMessage",
        data(){
            return{
                hire:{}
            }
        },

        methods:{
            addHireInfo(){
               this.$http.post("http://localhost:8080/lianxi/a/insert",this.hire).then(res=>{
                 if(res.data.success){
                     this.$router.push("/info");//路由切换

                 }
               });
            }
        }



    }
</script>

<style scoped>

</style>



